.controls {
    display: flex;
    background-color: var(--background-dark);
    border-radius: 3rem;
    padding: .6rem .5rem;
    justify-content: space-between;
  
}

.appButton {
    outline: none !important;
    border:none;
    background-color: transparent;
    color: white;
    opacity: 0.5;
    padding: 1.5rem 2.5rem;
    cursor: pointer;
    white-space: nowrap;
    
    
}

.active {
  color: var(--color-background);
    background-color: var(--color-primary);
    opacity: 1;
    border-radius: 3rem;
    font-weight: bold;
}

.appButton:not(:last-child){

    margin-right: .2rem;
}