.timer {
   
    position: relative !important;
    cursor: pointer; 
    width: var(--diameter);
    /* overflow:hidden; */
    height: var(--diameter);
    background-color: var(--background-dark);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    
   transition: all .3s ease;
    
    
    box-shadow: 1.5rem 1rem 2rem 2rem rgba(0,0,0, .15),
                3rem 0 4rem 5rem rgba(0,0,0, .1),
                3rem 0 5rem 7rem rgba(0,0,0, .04),
                -3rem -4rem 5rem  rgba(38,43,85, 1),
                -3rem -4rem 5rem  rgba(38,43,85, 1),
                -3rem -4rem 5rem  rgba(38,43,85, 1);
              
               
}


.background {
    position: absolute;
   
    
    transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.background__circle {
      
    stroke-dasharray: calc(145 * 2 * 3.14)  calc(290/2 * 2 * 3.14);
    stroke-dashoffset: calc(145 * 2 * 3.14 - 700 );

}

.progress-ring {
    position: absolute;
   
    
   
  
  

}
        .progress-ring__circle {
            
          
            transform: rotate(-90deg);
    transform-origin: 50% 50%;
            transition: stroke-dashoffset 0.1s;

         stroke-dasharray: calc(130 * 2 * 3.14)  calc(130*2 * 2 * 3.14);
  stroke-dashoffset: calc(130 * 2 * 3.14  ); 
  stroke: var(--color-primary)
  

}




.timer__ring {
    /* border: 1px solid red; */
}

.timer__display {

    color: white;
    font-size: 7.5rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
    transition: all .5s ease;
}

.timer__control {

    color: white;
    text-align: center;
    font-size: 1.5rem;
    letter-spacing: 1.5rem;
    transition: all .2s ease;
}


