.app {

    width: 75%;
    /* border: 1px solid red; */
    margin: auto;
}



.pomodoro {
   
    display: grid;
    justify-content: center;
    /* grid-template-columns: min-content; */
    grid-auto-rows: min-content;
    grid-row-gap: 5rem;
    padding: 3rem;
    /* border: 1px solid red; */
    
}

.pomodoro__heading {

    color: white;
    font-size: 2.7rem;
    text-align: center;
}

.pomodoro__controls {
    
 z-index: 2;
    
}

.pomodoro__timer {
    justify-self: center;
    
}

.pomodoro__settings {
   
    justify-self: center;
    margin-top: 2rem;
}


.pomodoro__settings > div > i {

    color: white;
    opacity: 0.5;
    font-size: 2rem;
    cursor: pointer;
   
}



