


.settingsModal {
    position: fixed;
    z-index: 3;
   top: 0;
   left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .3);
    backdrop-filter: blur(2px);
   

    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;

}

.settingModal__content {

            background-color: white;
            min-width: 45%;
            /* height: 70%; */
            border-radius: 2.5rem;
            padding: 3rem;
            position: relative;

}

.settingModal__submit {
        position: absolute;
        bottom: -2rem;
        
        outline: none !important;
        border:none;
        font-size: 1.2rem;
        padding: 1.2rem 3.5rem;
        background-color: var(--color-primary);
        color: white;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 2rem;
}

.settingsModal__header {
        display: flex;
        justify-content: space-between;
        padding-bottom: 1.5rem;
        border-bottom: 1px solid rgba(0,0,0, .1);
}

.settingsModal__header h1 {
    color: var(--color-background)
}

.settingsModal__header > i {
    font-size: 2rem;
    color: rgba(0,0,0, .3);
    cursor: pointer
}

.settingsModal__body {
      border-bottom: 1px solid rgba(0,0,0, .1);
      padding: 2rem 0;
}


.settingsModal__body h3 {
    color: var(--color-background);
    font-size: 1.5rem;
    letter-spacing: .3rem;
    margin-bottom: 2rem;
}

.timeBox {

}

.timeBox__control >  p {
      color: rgba(0,0,0, .4);
      font-size: 1rem;
      font-weight: bold;
}

.timeBox__input {
    position: relative;

}

.timeBox__input > input {
            outline : none;
            border: none;
            padding: 1.5rem 1.5rem;
            background-color: rgba(238, 241, 250);
            border-radius: 9px;
            /* font-size: 1.5rem; */
            font-weight: bold;
            color: var(--color-background)
}

.timeBox__controls {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1rem;
    display: flex;
    flex-direction: column;
}

.timeBox__controls > i {
    color: rgba(0,0,0, .3);
    cursor: pointer
}

.settingsModal__footer {
   
      padding: 2rem 0;
}

.fontBox {
    margin-bottom: 2rem;
    border-bottom: 1px solid rgba(0,0,0, .1);
    padding: 2rem 0;
}

.fontBox__heading {
    color: var(--color-background);
    font-size: 1.5rem;
    letter-spacing: .3rem;
    

}

.fontBox__options {
    display: flex;
    justify-content: flex-end;


}

.font {
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    color: var(--color-background);
    background-color: rgba(238, 241, 250);
    cursor: pointer;
    transition: all .3s ease;

}

.font.active {
    background-color: var(--color-background);
    color: white;
}


