.colorBox {
    padding-bottom: 2rem;
}
.colorBox__heading {
    color: var(--color-background);
    font-size: 1.5rem;
    letter-spacing: .3rem;
    

}

.colorBox__options {
    display: flex;
    justify-content: flex-end;


}

.color {
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    cursor: pointer;
    transition: all .3s ease;


}

.color--1 {
    background-color: var(--color-primary);
}

.color--2 {
    background-color: var(--color-secondary);
}
.color--3 {
    background-color: var(--color-tetiary);
}





@media screen and (max-width: 768px) {
    .settingModal__content {
         width: 60%;
         
    
    }

    .settingsModal__body h3 {
        color: var(--color-background);
        font-size: 1.5rem;
        letter-spacing: .3rem;
        margin-bottom: 2rem;
        text-align: center;
    }
    .timeBox__control{
        display: flex;
        align-content: center;
        align-items: center;
        justify-content: space-between ;
    }

    .timeBox__control:not(:last-child){
        margin-bottom: 1rem;
    }
}



/* Animations */

.font:hover, .color:hover {
    transform: scale(1.1);
}














@media screen and (max-width: 500px) {
    .settingModal__content {
         width: 90%;
        
    
    }

    .colorBox__heading {
        text-align: center;
        margin: 2rem 0;
    
    }

    .colorBox__options {
        display: flex;
        justify-content: center;
        
    }
    .fontBox {
        margin-bottom: 0;
    }
    .fontBox__heading {
        text-align: center;
        margin-bottom: 2rem;
    
    }

    .fontBox__options {
        display: flex;
        justify-content: center;
        
    }



  
}

